.test {
	padding-left: 150px;
}

.father {
	position: relative;
	margin: 0;
	padding: 0;
}

.flexit {
	display: inline-flex;
	flex-direction: row;
	flex-wrap: wrap;
	margin-left: 25px;
	column-gap: 20px;
	row-gap: 10px;
}

.box {
	width: 300px;
	height: 40px;
	margin: 0;
	padding: 0;
	background-color: rgba(0,123,255, 0.5);
	box-shadow: 2px 5px;
}

.box:hover {
	background-color: rgba(0,123,255, 0.8);
}


.border {
  border: 1px solid black;
  text-align:  center;
}

.showSearchBar {
	width: 300px;
	position: relative;
	left: 1%;
	background-color: rgba(255, 0, 0, 0.5);
}

.showSearchBar:hover {
		background-color: rgba(255, 0, 0, 0.8);
}